<template>
  <div class="v-userinfos">
    <el-row>
      <el-col :span="4">
        
      </el-col>
      <el-col :span="20">
        {{userinfo.username}}
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="4">
        
      </el-col>
      <el-col :span="20">
        {{userinfo.email}}
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="4">
        
      </el-col>
      <el-col :span="20">
        {{userinfo.mobilePhoneNumber}}
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="4">
        
      </el-col>
      <el-col :span="20">
        {{userinfo.createdAt}}
      </el-col>
    </el-row>
    <el-button class="v-quit" @click="quit"></el-button>
  </div>
</template>

<script>
import bus from '../bus'
import { MessageBox } from 'element-ui'

export default {
  props: ['userinfo'],
  data () {
    return {
      disable: true,
      emial: ''
    }
  },
  methods: {
    quit () {
      MessageBox('', ', ?', {
        type: 'warning'
      }).then(() => {
        this.$http.get(bus._val.path + 'quit')
            .then((response) => {
              if (response.status === 200) {
                this.$router.push('/')
              } else {
                console.log(response.status)
              }
            })
      }).catch(() => {
        console.log('')
      })
    }
  }
}
</script>

<style lang="less">
  .v-userinfos {
    .el-row {
      margin-bottom: 18px;
      .el-col-20 {
        color: #888;
        line-height: 1.4;
      }
    }
    .v-quit {
      position: absolute;
      right: 10px;
      top: 0;
    }
  }
</style>



